import { FixedDoubleBridge } from "./bridge/fixed-double-bridge";
const leftIframe = document.getElementById("page-left");
const rightIframe = document.getElementById("page-right");
const metaViewport = document.querySelector("meta[name=viewport]");
Window.prototype.doubleArea = new FixedDoubleBridge(window, leftIframe, rightIframe, metaViewport, window.gestures);
