import { DoubleAreaManager } from "../fixed/double-area-manager";
import { BridgeGesturesAdapter } from "./fixed-gestures-bridge";
export class FixedDoubleBridge {
    constructor(window, leftIframe, rightIframe, metaViewport, gestures) {
        const listener = new BridgeGesturesAdapter(gestures);
        this.manager = new DoubleAreaManager(window, leftIframe, rightIframe, metaViewport, listener);
    }
    loadSpread(spread) {
        this.manager.loadSpread(spread);
    }
    setViewport(viewporttWidth, viewportHeight, insetTop, insetRight, insetBottom, insetLeft) {
        const viewport = { width: viewporttWidth, height: viewportHeight };
        const insets = {
            top: insetTop,
            left: insetLeft,
            bottom: insetBottom,
            right: insetRight,
        };
        this.manager.setViewport(viewport, insets);
    }
    setFit(fit) {
        if (fit != "contain" && fit != "width" && fit != "height") {
            throw Error(`Invalid fit value: ${fit}`);
        }
        this.manager.setFit(fit);
    }
}
